html {
  font-size: 62.5%;
  @include rmin(1336) {
    font-size: 0.732vw; } }


body {
  min-width: 1024px;
  background: white;
  color: $color-text-main;
  @include f(p-regular, 14, 20);
  @include smooth-font; }

.out {
  overflow: hidden; }

.outer-wrap {
  padding: 0 $outer-padding; }

.inner-wrap {
  padding-left: $inner-padding; }

.section {
  position: relative;
  margin: rem(80) 0;
  &:first-of-type {
    margin-top: rem(40); }
  > p {
    margin: 1em 0; }
  &--sub {
    .section__number {
      color: $color-text-grey; } }
  &--2-2 {
    margin-top: rem(190); }
  &--2-2-1 {
    margin-bottom: rem(150);
    .section__silouettes {
      bottom: rem(-42); }
    .arrow-1 {
      position: absolute;
      z-index: 2;
      top: 100%;
      left: 50%;
      margin: rem(70) 0 0 rem(-70); } } }

.section__number {
  position: absolute;
  top: 0;
  left: -$inner-padding;
  @include f(m-semibold, 12, 18); }

.section__silouettes {
  position: absolute;
  bottom: rem(-42);
  right: 0;
  z-index: 1;
  @include responsiveImg('silouettes', 623, 727);
  width: 60%; }

.network-1 {
  margin: rem(-110) auto;
  @include responsiveImg('network1', 1073, 758, 'jpg'); }

.network-2 {
  margin: rem(-30) auto;
  @include responsiveImg('network2', 1073, 764); }

.network-3 {
  margin: auto;
  @include responsiveImg('network3', 682, 512); }

.arrow-1 {
  @include responsiveImg('arrow1', 212, 172); }
